.recomendation-block{
    &-top{
        display: flex;
        margin-bottom: 20px;
        border-bottom: 1px solid #D5D5D5;
        &__link{
            display: block;
            font-style: normal;
            font-weight: bold;
            font-size: 24px;
            line-height: 30px;
            color: #9B9B9B;
            margin-right: 95px;
            padding-bottom: 10px;
            &:last-child{
                margin-right: 0;
            }

            &:first-child{
                color: #4E72CA;
                border-bottom: 2px solid #4E72CA; ;
            }
        }
    }

    &-center{
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid rgba(182, 202, 251, 0.606827);
    }

    &-bottom{
        text-align: center;
        padding-bottom: 23px;
        padding-top: 18px;

        &__btn{
            border: none;
            background-color: #fff;
            outline: none;
            font-size: 15px;
            line-height: 18px;
            color: #4E72CA;
            cursor: pointer;
            position: relative;

            &::after{
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                right: -14px;
                margin: auto;
                background-repeat: no-repeat;
                width: 12px;
                height: 10px;
                background-image: url(../../assets/images/arrow-bottom.svg);
            }
        }
    }
}