.add-card{
    width: 359px;
    display: flex;
    background: #B4E2E1;
    border-radius: 10px;
    padding: 24px 31px 41px 30px;
    position: relative;
    height: 238px;
    box-sizing: border-box;
    overflow: hidden;
    user-select: none;
    margin-right: 30px;
    &.additional{
        background-color: #F7E233;

        .add-card__img:nth-child(2) {
            top: 0;
            margin: 0;
        }
    }
    .triangle{
        width: 0; 
        height: 0; 
        border-top: 700px solid transparent;
        border-bottom: 500px solid transparent;
        transform: rotate(39deg);
        border-left: 200px solid #FF9E83;
        position: absolute;
        right: -100px;
        top: 0;
        bottom: 0;
        margin: auto;
        z-index: 0;

        &.additional{
            border-color: #05F3B3;
        }
    }

    &__img{
        position: absolute;
        z-index: 2;
        right: 0;
        top: 0;
        bottom: 0;
        margin: auto;
    }

    &-left{
        &__title{
            font-size: 23px;
            line-height: 26px;
            color: #000000;
            width: 170px;
            margin-bottom: 77px;
        }

        &__info{
            font-size: 15px;
            line-height: 18px;
            color: #4A4A4A;
            display: block;
            z-index: 19;
            position: relative;
            &:after{
                content: '';
                position: absolute;
                width: 16px;
                height: 16px;
                background-repeat: no-repeat;
                background-position: center;
                top: 0;
                bottom: 0;
                margin: auto;
                right: 30px;
                margin-top: 2px;
                background-image: url(../../assets/images/arrow-bottom.svg);
                transform: rotate(-90deg);
                z-index: 13;
            }
        }
    }
}