.sale-block{
    &-link{
        font-style: normal;
        font-weight: 900;
        font-size: 24px;
        line-height: 30px;
        color: #9B9B9B;
        padding-bottom: 10px;
        display: block;

        &:first-child{
            margin-right: 77px;
            border-bottom: 1px solid #4E72CA;
            color: #4E72CA;
        }
    }

    &-top{
        border-bottom: 1px solid #D5D5D5;
        display: flex;
        margin-bottom: 10px;
    }

    &-center{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        padding-bottom: 26px;
        margin-bottom: 21px;
        border-bottom: 1px solid rgba(182, 202, 251, 0.606827);

        &-secondary{
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
        }
    }

    &-bottom{
        text-align: center;
        padding-bottom: 20px;

        &__btn{
            border: none;
            background-color: #fff;
            outline: none;
            font-size: 15px;
            line-height: 18px;
            color: #4E72CA;
            cursor: pointer;
            position: relative;

            &::after{
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                right: -14px;
                margin: auto;
                background-repeat: no-repeat;
                width: 12px;
                height: 10px;
                background-image: url(../../assets/images/arrow-bottom.svg);
            }
        }
    }
}