.home {
    margin-top: 36px !important;

    .slick-track {
        display: flex !important;
        justify-content: space-between !important;
        width: 100% !important;
    }

    .hover-block {
        width: 0;
        height: 0;
        overflow: hidden;

        &__link{
            display: block;
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 39px;
            letter-spacing: -6.7332e-05px;
            color: #000000;
            position: relative;

            &:hover{
                opacity: 0.6;
            }

            &::before{
                content: '';
                position: absolute;
                width: 10px;
                height: 10px;
                left: -15px;
                top: 0;
                bottom: 0;
                margin: auto;
                border-radius: 50%;
                background: #B4E2E1;
            }
        }
    }

    .slider-card:hover .hover-block {
        display: block !important;
    }

    .slick-slide:hover .hover-block {
        display: block !important;
    }

    .slider {
        position: relative;

        &:hover .hover-block {
            width: 1110px;
            height: 244px;
            background: #fff;
            position: absolute;
            left: 0;
            right: 0;
            overflow: visible;
            margin: auto;
            box-shadow: 2px 4px 15px rgba(0, 0, 0, 0.209296);
            top: 112px;
            z-index: 13;
            display: flex;
            padding: 29px 62px 29px 29px;
            box-sizing: border-box;
            justify-content: space-between;

            &::before{
                content: '';
                position: absolute;
                top: -11px;
                left: 0;
                right: 0px;
                margin: auto;
                width: 0; 
                height: 0; 
                border-left: 10px solid transparent;
                border-right: 10px solid transparent;
                
                border-bottom: 10px solid rgba(0, 0, 0, 0.209296);
            }
        }

        cursor: pointer;

        margin-bottom: 31px;

        .slick-arrow.slick-prev {
            position: absolute !important;
            z-index: 6;
            left: -5% !important;
            top: 0;
            bottom: 0;
            margin: auto;
            cursor: grab !important;
            background-color: #fff !important;
            border-radius: 50%;
            width: 34px;
            height: 34px;
            box-shadow: 1px 3px 6px rgba(0, 0, 0, 0.129331);

            &::before {
                content: '';
                width: 25px;
                height: 25.26px;
                background-image: url(../../assets/images/carousel-arrow.svg);
                position: absolute;
                z-index: 99;
                background-repeat: no-repeat;
                top: 14px;
                bottom: 0;
                margin: auto;
                right: 0;
            }

            &:active {
                cursor: grabbing !important;
            }
        }

        .slick-arrow.slick-next {
            position: absolute !important;
            z-index: 6;
            right: -5% !important;
            top: 0;
            bottom: 0;
            margin: auto;
            cursor: grab !important;
            background-color: #fff !important;
            border-radius: 50%;
            width: 34px;
            height: 34px;
            box-shadow: 1px 3px 6px rgba(0, 0, 0, 0.129331);

            &::before {
                content: '';
                width: 25px;
                height: 25.26px;
                background-image: url(../../assets/images/carousel-arrow.svg);
                position: absolute;
                z-index: 99;
                background-repeat: no-repeat;
                top: 0px;
                bottom: 7px;
                margin: auto;
                left: 0;
                transform: rotate(180deg);
            }

            cursor: grab !important;

            &:active {
                cursor: grabbing !important;
            }
        }
    }

    // .main-block{
    //     // margin-bottom: 100px;
    // }

    .add-block {
        background: rgba(182, 202, 251, 0.159986);
        box-shadow: inset 0px 21px 13px rgba(182, 202, 251, 0.225402);
        padding: 50px 0 42px 0;
        margin-bottom: 26px;

        .slick-arrow.slick-prev {
            display: none !important;
        }

        .slick-arrow.slick-next {
            display: none !important;
        }

        .slick-slider.slick-initialized {
            width: 1140px;
        }

        &-inner {
            display: flex;
            justify-content: space-between;
            user-select: none;
        }
    }
}